const clientsSlider = new Swiper(".clients-slider", {
    // slidesPerView: "auto",
    slidesPerView: 1,
    centeredSlides: true,
    spaceBetween: 8,
    pagination: {
        el: ".swiper-pagination",
        clickable: true,
    },
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },
    // watchOverflow: true,
    // navigation: false,
    breakpoints: {
        992: {
            slidesPerView: 2,
            spaceBetween: 32,
            centeredSlides: false,
        }
    },
    on: {
        slideChangeTransitionStart: function () {
            document.querySelectorAll('.clients-slider .collapse.show').forEach((el) => {
                bootstrap.Collapse.getInstance(el).hide();
            })
        }
    }
});

document.querySelectorAll('.clients-slider__description').forEach((item) => {
    item.addEventListener('hidden.bs.collapse', function () {
        item.closest('.clients-slider__item').classList.remove('border-primary');
    });
    item.addEventListener('shown.bs.collapse', function () {
        item.closest('.clients-slider__item').classList.add('border-primary');
    });
})