(function () {
    'use strict';

    // tilt images
    VanillaTilt.init(document.querySelectorAll(".image-tilt"), {
        max: 10
    });

    AOS.init({
        offset: 120,
        duration: 800,
        // easing: 'ease-in-sine',
        // delay: 100,
        once: true
    });

    // "welcome" blur animate
    document.addEventListener('aos:in', ({ detail }) => {
        if(detail.hasAttribute('data-aos-class')) {
            detail.classList.add(detail.getAttribute('data-aos-class'));
        }
    });

    // show modal from startup
    // const demoModalEl = document.getElementById('modalForm');
    // const demoModal = bootstrap.Modal.getOrCreateInstance(demoModalEl);
    // demoModal.show();

    // console.log(demoModal);

    // "validation" form
    Array.prototype.slice.call(document.querySelectorAll('.needs-validation'))
        .forEach(function (form) {
            form.addEventListener('submit', function (event) {
                if (!form.checkValidity()) {
                    event.preventDefault()
                    event.stopPropagation()
                }

                form.classList.add('was-validated')
            }, false)
        })

})()