const newsSlider = new Swiper(".news-slider", {
    slidesPerView: "auto",
    // slidesPerView: 1,
    // centeredSlides: true,
    spaceBetween: 8,
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },
    breakpoints: {
        // when window width is >= 640px
        768: {
            slidesPerView: 2,
            spaceBetween: 32,
            centeredSlides: false
        },
        1200: {
            slidesPerView: 3,
            spaceBetween: 32,
            centeredSlides: false
        }
    }
});